.main {
  width: 100wh;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
}

.btn {
  padding: 20px;
  background-color: darkseagreen;
  color: white;
  font-weight: 700;
  width: 200px;
  margin: auto;
  text-align: center;
  border-radius: 10px;
  margin-bottom: 50px;
}

.btn:hover {
  background-color: #fff;
  border: 1px solid darkseagreen;
  color: darkseagreen;
  cursor: pointer;
}

.data {
  width: 100wh;
  margin: auto;
}
